<template>
    <div></div>
</template>

<script type="text/ecmascript-6">
    import {getLoginUserInfo} from '@/api/UserApi';
    import Cookies from 'js-cookie';
    export default {
        mounted() {
            this.getUserLogin();
        },

        methods: {
            getUserLogin() {
              let Ttoken = this.$route.query['auth-token'];
              getLoginUserInfo({token: Ttoken}).then(response => {
                    let result = response.data;
                    let loginModel = result.data;
                    Cookies.set(this.$global.requestToken, Ttoken);
                    //本地存储用户登录的数据，这里是以json格式存储，用localStorage.setItem()正确存储json对象方法是：存储前先用JSON.stringify()方法将json对象转换成字符串形式
                    localStorage.setItem("user-center-token",JSON.stringify(loginModel));
                    this.$router.push({path: '/'})
                }).catch(error => {

                });
            }
        }

    }
</script>

<style rel="stylesheet" lang="css">

</style>



